/* Unna-300 - latin */
@font-face {
	font-family: 'Unna';
	font-style: normal;
	font-weight: 300;
	/* src: url('unna/unna-300.eot'); IE9 Compat Modes */
	src: local('Unna Light'), local('Unna-Light'),
	/* url('unna/unna-300.eot?#iefix') format('embedded-opentype'), IE6-IE8 */
	/* url('unna/unna-300.woff2') format('woff2'), Super Modern Browsers */
	/* url('unna/unna-300.woff') format('woff'), Modern Browsers */
	url('unna/Unna-Light.otf') format('opentype'), /* Safari, Android, iOS */
	/* url('unna/unna-300.svg#Unna') format('svg'); Legacy iOS */
}
/* Unna-300italic - latin */
@font-face {
	font-family: 'Unna';
	font-style: italic;
	font-weight: 300;
	/* src: url('unna/unna-300italic.eot'); IE9 Compat Modes */
	src: local('Unna Light Italic'), local('Unna-LightItalic'),
	/* url('unna/unna-300italic.eot?#iefix') format('embedded-opentype'), IE6-IE8 */
	/* url('unna/unna-300italic.woff2') format('woff2'), Super Modern Browsers */
	/* url('unna/unna-300italic.woff') format('woff'), Modern Browsers */
	url('unna/Unna-LightItalic.otf') format('opentype'), /* Safari, Android, iOS */
	/* -300italic.svg#Unna') format('svg'); Legacy iOS */
}
